.loadingDiv {
    margin-top: 15px;
    display: flex;
    position: absolute;
    opacity: 70%;
    justify-content: center;
    align-items: center;
    justify-self: center;
    width: 280px;
    height: 290px;
    overflow: hidden;
    border-radius: 15px;
}

.loadingImg {
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
