*{
    box-sizing: border-box;
}
.header {
    grid-area: header;
    background-color: chartreuse;
    display: flex;
    align-items: center;
}

@media (min-width: 768px){
    .btn_menu{
        display: none;
    }
}
/*@media (max-width: 992px) {*/
/*    .header{*/
/*        width: 768px;*/
/*    }}*/
.btn_menu{
    width: 2rem;
    height: 2rem;
    background-color: white;
    color: cornflowerblue;
    cursor: pointer;
    border-radius: 20%;
    font-size: 1.5rem;
    text-align: center;
    /*margin:0px auto;*/
    padding: 2px 0px 0px 0px;

}
.btn_logOut{
    width: 2rem;
    height: 2rem;
    background-color: white;
    color: cornflowerblue;
    cursor: pointer;
    border-radius: 50%;
    font-size: 1.5rem;
    text-align: center;
    margin:0px auto;
    padding: 0px;
    padding: 2px 0px 0px 0px;

}
.spacer{
    flex: 1;
    text-align: center;
}
.logoBrandH2{
    padding: 1rem;
    font-family: "Freestyle Script";
    font-size: 2rem;
}
.userProfile{
    padding: 0px 0px;
    font-family: "Freestyle Script";
    font-size: 2rem;
}
.userName{
    padding: 0px 5px 0px 0px ;

}
.header img {
    width: 45px;
    padding: 3px;
    margin: 0px 0px 0px 10px;
}
.loginBlock{
    margin: 10px 10px 0px 0px;
    padding: 10px;
    float: right;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.loginBlock a{
    color: black;
    font-size: larger;
}
.logoBrand{
    /*border-radius: 5% 30% 5% 30%;*/
    width: 200px;
    height: 200px;
}
