
.content {
    grid-area: content;
    display: grid;
    background-color: darkorange;
    grid-template-areas: "content_video" "content_description" "content_posts";
    grid-template-rows: 2fr 3fr 0.5fr;
    grid-template-columns: 12fr;
    overflow: hidden;
}

