.content__video{
    grid-area: content_video;
    position: relative;
    /*top: 60px;*/
    /*bottom: 0;*/
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.content__video video {
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.description {
    padding: 10px;
    grid-area: content_description;
    min-width: 100px;
    display: grid;
    grid-template-rows: 180px;
    grid-template-columns: 2fr 10fr;
    grid-template-areas: "content_logo content_description";
}

.logo {
    grid-area: content_logo;
    width: 100%;
    height: 100%;
    padding: 0px 10px 0px 10px;
}

.logo label>img {
    width: 90%;
    height: 90%;
    border-radius: 25%;
}
.logo label>img:hover{
    cursor: pointer;
}
.descriptionText {
    grid-area: content_description;
    width: 80%;
    /*max-width: 600px;*/
    padding: 10px 0px 0px 15px;
}

.status {
    padding: 10px 0px 10px 15px;
    font-size: 20px;
    color: white;
}
.status :hover{
    cursor: pointer;
}


.hiddenInputFile {
    visibility: hidden;
}

.buttonSaveChanges {
    width: fit-content;
    padding: 3px 10px 3px 10px;
    /*margin-top: 5px;*/
    margin: 10px;
    border-radius: 3px;
    background-color: teal;
    color: white;
}
